.box-shadow-1:after,
.box-shadow-1:before {
	top: 80%;
	left: 5px;
	width: 50%;
	z-index: -1;
	content: "";
	bottom: 15px;
	max-width: 300px;
	position: absolute; 
}

/* Shadow 1 */
.box-shadow-1 {
	-webkit-box-shadow: 0 14px 6px -6px #666;
	   -moz-box-shadow: 0 14px 6px -6px #666;
			box-shadow: 0 14px 6px -6px #666;
}
